        </svg></span>
            </li>
            <li>
               <img alt="app icon" class="logo icon"
                          :src="logo"
                          style="color:transparent !important;"> 
            </li>
          </ol>
        </div>
        <div v-if="!isIOS">
          <button class="btn btn-install" @click.stop="dispatchInstallPrompt">Install as PWA</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const WebClipDetect = {
  getDevice() {
    let ua = String(navigator.userAgent);
    return ua.match(/iPhone|iPod/) ? 'iPhone' : (ua.match(/iPad/) ? 'iPad' : this.isPadPro() );
  },
  isPadPro() {
    if ((navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 0) || navigator.platform === 'iPad') {
      return 'iPad';
    }
    return null
  },
  isIPhone() {
    return this.getDevice() == 'iPhone';
  },
  isIPad() {
    return this.getDevice() == 'iPad';
  },
  isPWA() {
    return navigator.standalone === true || !!(window.matchMedia('(display-mode: standalone)').matches);
  },
  isIos() {
    return !!(this.getDevice())
  },
  isSafari() {
    return !!navigator.userAgent.match(/Version\/[\d\.]+.*Safari/);
  },
  getIosDevice() {
    return this.getDevice();
  }
};


export default {
  name: "pwa-installer",

  props: {
    logo: {
      type: String,
      default() {
        return `data:image/svg+xml;base64,`;
      },
    },
    appName: {
      type: String,
      default() {
        return `Sample PWA`;
      }
    }
  },

  data() {
    return {
      canInstallPrompt: false,
      fullScreen: false,
    };
  },

  computed:{
    isIOS() {
      return WebClipDetect.isIos();
    },
    isDebug() {
      const q = new URLSearchParams(location.search);
      return q.has('debug');
    },
    isPWA() {
      return WebClipDetect.isPWA();
    },
    show() {
      if (this.isPWA) {
        return false;
      }
      return !this.isDebug && this.supportPWA;
    },
    chromeSupport() {
      if (!(navigator.getInstalledRelatedApps instanceof Function)) {
        return false;
      }
      return this.canInstallPrompt;
    },
    supportPWA() {
      return (WebClipDetect.isSafari() && WebClipDetect.isIos()) || this.chromeSupport;
    }
  },

  created() {
    this.canShowInstallButton();
    window.addEventListener('appinstalled', this.handlePWAInstalled);
  },

  beforeDestroy() {
    window.removeEventListener('appinstalled', this.handlePWAInstalled);
  },

  methods: {
    toggleFullScreen() {
      this.fullScreen = !this.fullScreen;
    },
    handleCloseOverlay() {
      this.fullScreen = false;
    },
    handlePWAInstalled() {
      this.canInstallPrompt = false;
    },
    canShowInstallButton() {
      const ok = !!(window.promptEvent);
      if (ok) {
        this.canInstallPrompt = true;
        return Promise.resolve();
      }
      return new Promise((resolve) => {
        const handler = (event) => {
          window.removeEventListener("beforeinstallprompt", handler);

          event.preventDefault();
          window.promptEvent = event;
          this.canInstallPrompt = true;
          resolve();
        };

        window.addEventListener('beforeinstallprompt', handler);
      })
    },
    dispatchInstallPrompt() {
      const promptEvent = window.promptEvent;

      if (promptEvent) {
        promptEvent.prompt();  // Wait for the user to respond to the prompt
        promptEvent.userChoice
            .then(choice => {
              if (choice.outcome === 'accepted') {
                console.log('User accepted');
              } else {
                console.log('User dismissed');
              }
            })
      }
    },
  }
}
</script>

<style lang="scss">
@import "normalize.css";

$desktop_layout_width: 2048;
$mobile_layout_width: 1536;
$base_rem: 24px;

@function d-vw($target) {
  $vw-context: ($desktop_layout_width*.01) * 1px;
  @return calc($target/$vw-context) * 1vw;
}

@function m-vw($target) {
  $vw-context: ($mobile_layout_width*.01) * 1px;
  @return calc($target/$vw-context) * 1vw;
}

@function rem($target) {
  @return calc($target/$base_rem) * 1rem;
}
html {
  font-size: d-vw($base_rem);
}
@media (max-width: 1024px) {
  html {
    font-size: m-vw($base_rem);
  }
}
.installer-wrap {
  .btn {
    border: 0;
    border-radius: 5px;
    background-color: #000000;
    color: #ffffff;
    cursor: pointer;
  }
  .btn-install {
    display: block;
    padding: 1em;
    margin: 1em 0;
    width: 100%;
  }
  .mini-ui {
    position: fixed;
    z-index: 1100;
    right: -60px;
    bottom: 10px;
    opacity: 0.5;
    cursor: pointer;

    &:hover {
      transition: all 0.5s ease;
      right: 10px;
      opacity: 1;
    }

    > .icon {
      display: inline-block;
      width: 35vw;
      max-width: 150px;
      height: auto;
    }
  }
}
.install-wrap {
  left: 0;
  top: 0;
  position: fixed;
  z-index: 1100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255,255,255,0.95);
  display: flex;
  justify-content: center;
  align-items: center;

  .inner {
    width: rem(700px);
    background-color: white;
    font-size: rem(32px);
    line-height: 2;
    padding: 0 1em;
    border-radius: 8px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  }

  .header, .details, .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
  }
  .header {
    padding: 1em 0;
  }

  .details {
    border-radius: 5px;
    background-color: rgba(0,0,0,0.1);
  }
  .content {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    li {
      align-items: center;
    }
    ol {
      padding-left: 0.5em;
    }
  }

  .icon {
    width: rem(35px);
    height: auto;
    margin: 0 0.25em;
    vertical-align: baseline;
  }
  .logo {
    width: rem(150px);
    height: auto;

    &.icon {
      width: rem(35px);
    }
  }
  .close {
    cursor: pointer;
  }
  .add-home {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    padding: 0.25em 1em;
    display: inline-block;
    line-height: 1;

    .icon {
      display: inline-block;
      visibility: visible;
    }
  }
}
</style>
